@use 'base/mixins' as m;
@use 'base/variables' as v;

.speakers {
    &__grid {
        @include m.contenedor;
        @include m.grid(1, 5rem);
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
        @include m.xl_desktop {
            @include m.grid(3, 5rem);
        }
    }
}

.speaker {
    @include m.card;
    &__ubicacion {
        color: v.$gris;
        margin: 1rem 0;
        font-size: 2rem;
        line-height: 1.5rem;
    }
    &__listado-skills {
        @include m.tags;
    }
}

.speaker-sociales {
    @include m.menu-social(v.$negro, 3rem);
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
}